* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  list-style: none;
  font-size: 2rem;
}
.country-list li {
  display: flex;
  gap: 0.35em;
  align-items: center;
  cursor: pointer;
}
.country-list img {
  height: 1em;
}
.country-list p {
  margin: 0;
  line-height: 2em;
}
.country-info {
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.3em;
}

.country-info div:first-child {
  font-size: 2.5rem;
  margin-bottom: 0.3em;
}
.country-info h3 {
  margin: 0;
}
.country-info h4,
.country-info p {
  margin: 0;
}
.country-info div {
  display: flex;
  align-items: center;
  gap: 1em;
}
.country-info img {
  height: 1em;
}
